import React from "react";
import styled from "styled-components";
// Components
import ProjectBox from "../Elements/ProjectBox";
import FullButton from "../Buttons/FullButton";
// Assets
import ProjectImg1 from "../../assets/img/projects/1.png";
import ProjectImg2 from "../../assets/img/projects/2.png";
import ProjectImg3 from "../../assets/img/projects/3.png";
import AddImage2 from "../../assets/img/add/add2.png";

export default function Projects() {
  const whatsappNumber = "5591985735084";
  const whatsappMessage = "Ol, gostaria de saber mais sobre as solues de e-commerce da Bel Tech.";

  return (
    <Wrapper id="projects">
      <div className="whiteBg">
        <div className="container">
          <HeaderInfo>
            <h1 className="font40 extraBold">Nossas Solues de E-commerce</h1>
            <p className="font13">
              Descubra como nossas plataformas oferecem vantagens nicas para diversos setores.
            </p>
          </HeaderInfo>
          <div className="row textCenter">
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg1}
                title="E-commerce para Pets"
                text="Descomplicao total com pagamento automatizado e gesto de produtos intuitiva. Acesso fcil a preos justos e uma experincia de compra sem complicaes para os amantes de pets."
                action={() => window.location.href = "https://bel-pets-production.up.railway.app/"}
              />
            </div>
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg2}
                title="E-commerce para Restaurantes"
                text="Automatizao de processos de pagamento, garantindo transaes seguras e rpidas. Oferecemos preos competitivos e uma interface que torna a compra de alimentos online simples e acessvel."
                action={() => window.location.href = "https://bel-food-78546.web.app/"}
              />
            </div>
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg3}
                title="E-commerce para Mercados"
                text="Solues de pagamento automatizado que facilitam o checkout. Garantimos preos justos e uma experincia de compra descomplicada, aumentando a acessibilidade para todos os consumidores."
                action={() => alert("A plataforma de compras ainda no est pronta, mas fique atento para futuras atualizaes!")}
              />
            </div>
          </div>
          <div className="row flexCenter">
            <div style={{ margin: "50px 0", width: "200px" }}>
            </div>
          </div>
        </div>
      </div>
      <div className="lightBg">
        <div className="container">
          <Advertising className="flexSpaceCenter">
            <AddLeft>
              <AddLeftInner>
                <ImgWrapper className="flexCenter">
                  <img className="radius8" src={AddImage2} alt="add" />
                </ImgWrapper>
              </AddLeftInner>
            </AddLeft>
            <AddRight>
              <h4 className="font15 semiBold">Sobre a Bel Tech</h4>
              <h2 className="font40 extraBold">Inovao em E-commerce</h2>
              <p className="font12">
                Na Bel Tech, revolucionamos a experincia de compra online com solues de e-commerce rpidas, intuitivas e sem complicaes. Nossa equipe especializada est dedicada a fornecer plataformas robustas e eficientes, garantindo a satisfao do cliente em cada setor que atendemos.
              </p>
              <ButtonsRow className="flexNullCenter" style={{ margin: "30px 0" }}>

                <div style={{ width: "190px", marginLeft: "15px" }}>
                  <FullButton
                    title="Entre em Contato"
                    action={() => window.location.href = `https://api.whatsapp.com/send?phone=${whatsappNumber}&text=${encodeURIComponent(whatsappMessage)}`}
                    border
                  />
                </div>
              </ButtonsRow>
            </AddRight>
          </Advertising>
        </div>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
`;
const HeaderInfo = styled.div`
  @media (max-width: 860px) {
    text-align: center;
  }
`;
const Advertising = styled.div`
  padding: 100px 0;
  margin: 100px 0;
  position: relative;
  @media (max-width: 1160px) {
    padding: 60px 0 40px 0;
  }
  @media (max-width: 860px) {
    flex-direction: column;
    padding: 0 0 30px 0;
    margin: 80px 0 0px 0;
  }
`;
const ButtonsRow = styled.div`
  @media (max-width: 860px) {
    justify-content: space-between;
  }
`;
const AddLeft = styled.div`
  position: relative;
  width: 50%;
  p {
    max-width: 475px;
  }
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
    text-align: center;
    h2 {
      line-height: 3rem;
      margin: 15px 0;
    }
    p {
      margin: 0 auto;
    }
  }
`;
const AddRight = styled.div`
  width: 50%;
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
  }
`;
const AddLeftInner = styled.div`
  width: 100%;
  position: absolute;
  top: -300px;
  left: 0;
  @media (max-width: 1190px) {
    top: -250px;
  }
  @media (max-width: 920px) {
    top: -200px;
  }
  @media (max-width: 860px) {
    order: 1;
    position: relative;
    top: -60px;
    left: 0;
  }
`;
const ImgWrapper = styled.div`
  width: 100%;
  padding: 0 15%;
  img {
    width: 100%;
    height: auto;
  }
  @media (max-width: 400px) {
    padding: 0;
  }
`;
